export default {
	data() {
		return {
			list:[],
			keywords:'',
			nav_0:true,
			nav_1:false,
			status:0,
		}
	},
	onLoad(e) {
		var t=this;	
		t.getList();
	},
		
	methods: {
		goDetail:function(id){
			var t=this;
			uni.navigateTo({
				url: 'survey-detail?id='+id+'&status='+t.status
			})
		},
		getList:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				status:t.status,
			}
			t.urlRequest.urlRequest(t.func.getSurveyList,data,function(res){
				t.list=res.data.survey_list;
				uni.hideLoading();
			})
		},
		navChange:function(status){
			var t=this;
			t.list=[];
			if(status==0){
				t.nav_0=true;
				t.nav_1=false;
				t.status=0;
				t.getList();
			}else{
				t.nav_1=true;
				t.nav_0=false;
				t.status=1;
				t.getList();
			}
		}
	}
}
